:root {
  --color-bg-code: #f9f4ff;
  --color-accent: #f0c4e7;
  --color-secondary: #cab8f1;
  --color-tertiary: #7acaff;
  --color-text: #756a85;
  --color-light: #ffffff;
  --border-radius: 12px;
}

code:not(pre > code) {
  background: linear-gradient(135deg, var(--color-bg-code) 0%, #f0e6ff 100%);
  color: var(--color-text);
  padding: 0.2em 0.4em;
  border-radius: 6px;
  font-size: 0.9em;
  border: 1px solid #e8d7ff;
  box-shadow: 0 2px 3px rgba(178, 141, 255, 0.1);
}

pre {
  background: linear-gradient(to bottom right, #faf4ff, #f0e9ff);
  border-radius: var(--border-radius);
  padding: 1.5em 1.2em 1.2em;
  overflow: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
  max-height: 400px;
  border: 2px solid #e8d7ff;
  box-shadow: 0 5px 15px rgba(178, 141, 255, 0.15), 
              inset 0 0 15px rgba(255, 255, 255, 0.5);
  position: relative;
  margin: 1.5em 0;
}

pre code {
  background: transparent !important;
  font-size: 0.95em;
  color: var(--color-text);
  display: block;
  padding: 0;
}

.hljs-keyword { color: #ff6bd6; font-weight: 500; }
.hljs-string { color: #7acaff; }
.hljs-function { color: #b28dff; }
.hljs-comment { color: #c0a1e0; font-style: italic; }
.hljs-number { color: #ff9d6c; }

.copy-code-button {
  color: var(--color-light);
  background: linear-gradient(to right, var(--color-accent), var(--color-secondary));
  border: none;
  border-radius: 20px;
  padding: 6px 12px;
  font-size: 0.8rem;
  position: absolute;
  z-index: 1;
  right: 15px;
  top: 15px;
  margin: 0;
  transition: all 0.3s ease;
  opacity: 0.85;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  box-shadow: 0 2px 5px rgba(178, 141, 255, 0.3);
}

.copy-code-button:hover {
  opacity: 1;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(178, 141, 255, 0.4);
  cursor: pointer;
}

.copy-code-button:active {
  transform: translateY(0);
}

.copy-code-button > svg {
  fill: var(--color-light);
  height: 14px;
  width: 14px;
}

.highlight {
  position: relative;
  margin: 2em 0;
}

.language-label {
  position: absolute;
  left: 15px;
  top: -10px;
  background: linear-gradient(to right, var(--color-secondary), var(--color-tertiary));
  color: white;
  padding: 3px 12px;
  border-radius: 15px;
  font-size: 0.75rem;
  font-weight: 500;
  z-index: 2;
  box-shadow: 0 3px 5px rgba(178, 141, 255, 0.2);
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

pre code.hljs {
  display: block;
  overflow: auto;
  padding: 0;
  white-space: pre-wrap;
  max-height: 350px;
}